	// Usage
	// font-family: ff(body);
	// font-family: get-font-family(body);
	// @include set-font-family(display);

$font-families: (
	display: ( "Helvetica Neue", "Helvetica", "Arial", sans-serif ),
	body: ( "Georgia", "Times New Roman", "Times", serif ),
	code: ( "Source Code Pro", "Consolas", "Courier", monospace )
) !default;


/**
 * Checks if the specified font stack exists in the
 * typograpghy config and returns it if present
 */
@function get-font-family($key, $map:$font-families) {
	@if type-of($map) != map {
		@error "The passed param #{$map} is not a map.";
	}
	@if ( map-has-key($map, $key) ) {
		@return map-get($map, $key);
	} @else {
		@error "There is no value `#{$key}` in the $font-families map";
	}
}

@function ff($key) {
	@return get-font-family($key);
}


/**
 * Sets the font stack
 */
@mixin set-font-family($stack) {
	$fonts: get-font-family($stack);
	font-family: $fonts;
}
